:root {
  /* colors */
  --color-white: hsl(0, 0%, 100%);
  --color-black: hsl(0, 0%, 0%);
  --color-lite: hsl(0, 0%, 94%);
  --color-primary: hsl(218, 100%, 33%);
  --color-primary-lite: hsl(212, 68%, 40%);
  --color-secondary: hsl(150, 65%, 36%);
  --color-secondary-lite: hsl(156, 100%, 34%);
}

:root {
  --font-system: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, Helvetica, Arial, sans-serif;
  --font-body: Lato, var(--font-system);
  --font-heading: Myriad, var(--font-system);
  --line-height: 1.5;
  --line-height-sm: 1.25;
  --ratio: 1.25;
  --s0: 1rem;
  --s-1: calc(var(--s0) / var(--ratio));
  --s-2: calc(var(--s-1) / var(--ratio));
  --s-3: calc(var(--s-2) / var(--ratio));
  --s-4: calc(var(--s-3) / var(--ratio));
  --s-5: calc(var(--s-4) / var(--ratio));
  --s-6: calc(var(--s-5) / var(--ratio));
  --s1: calc(var(--s0) * var(--ratio));
  --s2: calc(var(--s1) * var(--ratio));
  --s3: calc(var(--s2) * var(--ratio));
  --s4: calc(var(--s3) * var(--ratio));
  --s5: calc(var(--s4) * var(--ratio));
  --s6: calc(var(--s5) * var(--ratio));
}

:root {
  /* Spacing */
  --f0: 1em;
  --f-1: calc(var(--f0) / var(--ratio));
  --f1: calc(var(--f0) * var(--ratio));
  --f2: calc(var(--f1) * var(--ratio));
  --f3: calc(var(--f2) * var(--ratio));
  --f4: calc(var(--f3) * var(--ratio));
  --f5: calc(var(--f4) * var(--ratio));
  --flow-0: 0;
  --flow-xs: var(--f-1);
  --flow-sm: var(--f0);
  --flow-md: var(--f1);
  --flow-lg: var(--f2);
  --flow-xl: var(--f3);
  --flow-xxl: var(--f4);
  --flow-xxxl: var(--f5);
  --gap-0: 0;
  --gap-xs: calc(var(--s0) / 1.5);
  --gap-sm: var(--s0);
  --gap-md: calc(var(--s0) * 1.5);
  --gap-lg: calc(var(--s0) * 3);
  --gap-xl: calc(var(--s0) * 4);
  --gap-xxl: calc(var(--s0) * 5);
  --gutter: var(--gap-md);
}

:root {
  /* Misc */
  --transition-all: 250ms ease all;
  --transition-fill: 250ms ease fill;
  --border-line: Max(1px, .1em);
  --radius-rounded: 0.25em;
  --radius-pill: 999em;
  --radius-circle: 50%;
}

html {
  font-size: calc(0.1vw + 0.1vh + 1em);
}

body {
  font-family: var(--font-body);
  background: var(--color-lite);
  color: var(--color-black);
  line-height: var(--line-height);
  position: relative;
}

a {
  color: var(--color-primary);
  text-decoration: none;
}
a:visited {
  color: var(--color-primary);
}
a:hover, a:focus {
  color: var(--color-secondary);
}
a:active {
  color: var(--color-secondary);
}
a svg {
  transition: var(--transition-fill);
}

h1,
.h1 {
  font-size: var(--s4);
  line-height: var(--line-height-sm);
}

h2,
.h2 {
  font-size: var(--s3);
  line-height: var(--line-height-sm);
}

h3,
.h3 {
  font-size: var(--s2);
  line-height: inherit;
}

h4,
.h4 {
  font-size: var(--s1);
  line-height: inherit;
}

h5,
.h5 {
  font-size: var(--s);
  line-height: inherit;
}

.h6 {
  font-size: var(--s-1);
  line-height: inherit;
}

hr {
  height: var(--border-line);
  background: var(--color-subtle-mute);
  border: 0;
}

.container {
  max-width: 75rem;
  padding-left: var(--gutter);
  padding-right: var(--gutter);
  margin-left: auto;
  margin-right: auto;
}

.row {
  --half-gutter: calc(var(--gutter) / 2);
  gap: var(--gutter);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

