.site-header {
  position: relative;
  background-color: var(--color-secondary);
}
.site-header > * {
  color: var(--color-white);
}

.site-nav a {
  font-weight: bold;
  color: var(--color-lite);
}
.site-nav a:hover, .site-nav a:focus {
  color: var(--color-white);
}
.site-nav a:active {
  color: var(--color-white);
}

:root {
  --grid-header-col: 1fr 1fr 2fr 1fr 12.5vw;
  --grid-header-row: auto 1rem;
}

@media (min-width: 62rem) {
  .header-container {
    display: grid;
    text-align: center;
    grid-template-columns: var(--grid-header-col);
    position: relative;
  }
}

@media (min-width: 62rem) {
  .site-brand {
    display: grid;
    grid-column: 1/-1;
    grid-row: 1/-1;
    place-items: center;
  }
}

@media (min-width: 62rem) {
  .site-logo {
    padding: var(--s2) var(--s4);
    display: inline-block;
  }
}

.site-logo > svg {
  fill: var(--color-white);
}

@media (min-width: 62rem) {
  .site-logo {
    box-shadow: 0 0 0 1rem var(--color-secondary), 0 0.5rem 1rem 0.5rem rgba(0, 0, 0, 0.5);
    position: relative;
    top: 0.5rem;
    z-index: 2;
  }
  .site-logo::before {
    content: "";
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--color-secondary);
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 62rem) {
  .site-nav {
    grid-column: 1/-1;
    grid-row: 1;
  }
}

@media (min-width: 62rem) {
  .site-nav-list {
    height: 100%;
    display: grid;
    grid-template-columns: var(--grid-header-col);
  }
}

@media (min-width: 62rem) {
  .site-nav-list li:last-child {
    grid-column: 4/5;
  }
}

@media (min-width: 62rem) {
  .site-nav-item {
    height: 100%;
  }
}

@media (min-width: 62rem) {
  .site-nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
}

.sr-only, .skip-link {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
  top: 0;
}

.skip-link:focus {
  border-radius: 0;
  position: absolute;
  clip: auto !important;
  -webkit-clip-path: none;
  clip-path: none;
  display: block;
  width: 100%;
  height: auto;
  left: 0;
  top: 0;
  z-index: 102;
}

