.link-block {
  position: static;
}
.link-block::before {
  content: "";
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.max-width-xxs {
  max-width: 18rem;
}

.max-width-xs {
  max-width: 36rem;
}

.max-width-sm {
  max-width: 48rem;
}

.max-width-md {
